import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { eventActions } from "@/processes/event"
import { EVENT_CURRENT_PAGE } from "@/processes/event/types/event-types"
import { EventTask } from "@/entities/EventTask/types/event-task-type"
import { FC, useEffect } from "react"
import { eventTaskActions, initialEventTasks } from "@/entities/EventTask"
import EventList from "./EventList"
import { EventItemResultType, eventItemActions } from "@/widgets/EventItem/model/EventItemSlice"



export const EventListContainer: FC<{}> = () => {
    const tasks = useAppSelector(state => state.eventTask.tasks)
    const isFetched = useAppSelector(state => state.eventTask.isFetched)
    const dispatch = useAppDispatch()


    useEffect(() => {

        !isFetched && dispatch(
            initialEventTasks()
        )

    }, [])

    const setResultMenuStatus = (
        status: EventItemResultType,
        task: EventTask
    ) => {

        dispatch(
            eventTaskActions
                .setCurrentTask(
                    { task }
                ))

        dispatch(
            eventItemActions
                .setEventItemMenuStatus(
                    {
                        status: true,
                        menuType: status

                    }

                )
        )

        dispatch(
            eventActions
                .setCurrentPage(
                    {
                        page: EVENT_CURRENT_PAGE.ITEM
                    }

                )
        )

    }





    return <EventList

        tasks={tasks}
        setResultMenuStatus={setResultMenuStatus}

    />

}



