import { FC } from "react";
import { EventTask } from "@/entities/EventTask/types/event-task-type";
import * as classes from './EventList.module.scss'
import {
    Table,
} from "reactstrap";

interface EventListProps {
    tasks: Array<EventTask>
    setResultMenuStatus: (
        status: 'result' | 'noresult' | 'expired' | 'cancel',
        task: EventTask
    ) => void
}


const EventList: FC<EventListProps> = ({
    tasks,
    setResultMenuStatus
}) => {




    return (


        <div className={`table-responsive p-4 ${classes.calling__page__wrapper}`}>
            {/* <h4 className="card-title">Запланированные дела по Компании</h4>
                        <p className="card-title-desc">Не завершенные задачи</p> */}

            <div className="table-responsive">
                <Table className={`align-middle mb-0`} >

                    <thead>
                        <tr  >
                            <th >#</th>
                            <th>Что надо сделать</th>
                            <th>Тип</th>
                            <th>Крайний срок</th>
                            <th>Текущий статус</th>
                            {/* <th>Комментарий</th> */}
                            <th>Действие</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            tasks && tasks.length && tasks.map((task, i) => (
                                <tr key={`table-row-${i}`}>
                                    <th scope="row">{i + 1}</th>
                                    <td>{task.name}</td>
                                    <td>{task.type}</td>
                                    <td>{task.deadline}</td>
                                    <td>
                                        {
                                            task.isExpired === 'no'
                                                ? <span className={classes.statusSuccess} >
                                                    Запланирован
                                                </span>


                                                // <span
                                                //     className={style.badge__success}
                                                // >
                                                //     {'Запланирован'}
                                                // </span>
                                                : task.isExpired === 'yes'
                                                    ? <span className={`${classes.statusWarning}`} >
                                                        Просрочен
                                                    </span>
                                                    : <span className={`${classes.statusExpired}`} >

                                                        {'Скоро'}
                                                    </span>
                                        }

                                    </td>
                                    <td >
                                        <div className={classes.actionsCell}>
                                            <button type="button" className={
                                                `btn btn-sm  mr-5 ${classes.badge__star}`
                                            }
                                                onClick={() => setResultMenuStatus('result', task)}
                                            >Результативный</button>
                                            <button type="button" className={
                                                `btn btn-sm ms-2 ${classes.badge__error}`
                                            }
                                                onClick={() => setResultMenuStatus('noresult', task)}
                                            >Не очень</button>
                                            <button type="button" className={
                                                `btn btn-sm ms-2 ${classes.badge__tolong}`
                                            }
                                                onClick={() => setResultMenuStatus('expired', task)}
                                            >Перенос</button>
                                            <button type="button" className={
                                                `btn btn-sm ms-2 ${classes.badge__cancel}`
                                            }
                                                onClick={() => setResultMenuStatus('cancel', task)}
                                            >Не считать</button>
                                        </div>

                                    </td>
                                </tr>
                            ))
                        }

                    </tbody>
                </Table>
            </div>

        </div>



    );
};

export default EventList